<script>

const modeType = ['repeatOne','repeatAll'];
const status = ['play','pause'];
export default {
  name: 'Player',
  props: {
    msg: String
  },
  data: function () {
    return {
      albums:
        [{
          albumTitle:'Quincas Moreira',
          cover_url:'',
          no:1,
          songTitle:'Metamorphosis',
          file_url:'https://www.youtube.com/audiolibrary_download?vid=86dd9259b534cc4d'       
          },
          {
            albumTitle:'Quincas Moreira',
            cover_url:'',
            no:2,
            songTitle:'Mariposa',
            file_url:'https://www.youtube.com/audiolibrary_download?vid=3d9ea88f85a351f8'       
          },
          {
            albumTitle:'Quincas Moreira',
            cover_url:'',
            no:2,
            songTitle:'Spy Suite',
            file_url:'https://www.youtube.com/audiolibrary_download?vid=f560266c20699a04'       
          }


          
        ],
      currentSongIdx:0,
      songTitle:null,
      albumTitle:null,
      play_song:null,
      playing: false,
      volume:50,
      process:0,
      mode:'repeatall',
      Shuffle: false,
      currentTime:0,
      maxTime:0,
      mouseDownCheck:false,
      canvas:null,
      canvasProp:{},
      ctx:null,
      analyser:null,
      audioSrc:null,
      frequencyData:null

    }
  },
  methods:{
    playHandle() {
      this.$refs.playC.play();
      this.playing = true;
      console.log('play event')
    },
    StopHandle() {
      this.$refs.playC.pause();
      this.playing = false;
      console.log('stop event')
    },
    PlayNextHandle() {
      if (this.currentSongIdx == this.albums.length - 1){
        this.currentSongIdx = 0;
      }else{
         this.currentSongIdx++;
      }      
      var music = this.albums[this.currentSongIdx];
      this.loadMusic(music);
    },
    PlayPreHandle() {
      if (this.currentSongIdx == 0){
        this.currentSongIdx = this.albums.length - 1
      }else{
         this.currentSongIdx--;
      }      
      var music = this.albums[this.currentSongIdx];
      this.loadMusic(music);
    },
    VolumeChangeHandle(e) {
      this.$refs.playC.volume = e.target.value / 100
    },
    ProcessChangeHandle(e) {
      var audioObj = this.$refs.playC
      var currentStatus = audioObj.paused;
      audioObj.currentTime = e.target.value / 1000 * this.maxTime
    },
    SwitchModeHandle() {
      let mode;
      switch (this.mode) {
        case 'repeatOne':
          mode = 'repeatAll'
          break
        case 'repeatAll':
          mode = 'random'
          break
        case 'random':
          mode = 'repeatOne'
          break
        default:
          mode = 'repeatOne'
          break
      }
      this.mode = mode;
    },
    toHHMMSS:function (sec) {
        var sec_num = parseInt(sec, 10); // don't forget the second param
        var hours   = Math.floor(sec_num / 3600);
        var minutes = Math.floor((sec_num - (hours * 3600)) / 60);
        var seconds = sec_num - (hours * 3600) - (minutes * 60);

        if (hours   < 10) {hours   = "0"+hours;}
        if (minutes < 10) {minutes = "0"+minutes;}
        if (seconds < 10) {seconds = "0"+seconds;}
        return hours+':'+minutes+':'+seconds;
    },
    getCurrentTime: function() {
      if (this.mouseDownCheck == true) return
      this.currentTime = this.$refs.playC.currentTime
      this.process = this.currentTime / this.maxTime * 1000
    },
    CheckMouseDown: function(){
        this.mouseDownCheck = true
    },
    CheckMouseUp: function(){
      this.mouseDownCheck = false
    },
    renderFrame:  function() {
      var array = new Uint8Array(this.analyser.frequencyBinCount);
      this.analyser.getByteFrequencyData(array);
      var step = Math.round(array.length / this.canvasProp.meterNum); //sample limited data from the total array
      this.ctx.clearRect(0, 0, this.canvasProp.cwidth, this.canvasProp.cheight);
      this.ctx.fillStyle = this.canvasProp.bg_gradient;
      this.ctx.fillRect(0, 0, this.canvasProp.cwidth, this.canvasProp.cheight);

      this.ctx.beginPath();
      this.ctx.moveTo(0, this.canvasProp.cheight / 2);
      this.ctx.lineTo(this.canvasProp.cwidth, this.canvasProp.cheight / 2);
      this.ctx.strokeStyle = this.canvasProp.lineColor;
      this.ctx.lineWidth = 0.5;
      this.ctx.stroke();
      for (var i = 0; i < this.canvasProp.meterNum; i++) {
          var value = array[i * step];
          if (this.canvasProp.capYPositionArray.length < Math.round(Math.abs(this.canvasProp.meterNum))) {
              this.canvasProp.capYPositionArray.push(value);
          };
          this.ctx.fillStyle = this.canvasProp.lineColor;
          this.ctx.fillRect(this.canvasProp.cwidth - i * 6 - this.canvasProp.meterWidth, (this.canvasProp.cheight - value) / 2, this.canvasProp.meterWidth, value); 
      }
      requestAnimationFrame(this.renderFrame);
    },
    loadMusic(music){
      var _this = this
      var audioObj = _this.$refs.playC
      this.play_song = music.file_url;
      _this.albumTitle = music.albumTitle;
      _this.songTitle = music.songTitle;
      this.process = 0;
      audioObj.load();
      audioObj.oncanplay = function(){
        _this.maxTime = audioObj.duration;
        if (_this.playing) audioObj.play();
      }
    },
    songendHandle(){
      var audioObj = this.$refs.playC
      if (this.mode == 'repeatOne'){
        audioObj.currentTime = 0
      }else if (this.mode == 'repeatall'){
        this.PlayNextHandle();
        // if (this.Shuffle == true){

        // }
      }
      // this.StopHandle();
      // console.log('sond end');
    }
  },
  computed: {
    calculatePercentage: function(){
      return this.currentTime / this.maxTime * 100;
    }
    // getCurrentTime: function() {
    //   console.log(this.$refs.playC);
    //   if (this.$refs.playC !== undefined){
    //       this.currentTime = this.$refs.playC.currentTime
    //       return this.$refs.playC.currentTime;
    //   }
    // }
  },
  watch: {
    process: function(){
      this.currentTime = this.$refs.process.value / 1000 * this.maxTime
    },
    volume: function(){
      this.$refs.playC.volume = this.$refs.volume.value / 100
    }
  },
  mounted(){
    var _this = this
    var audioObj = this.$refs.playC
    var audioSource = this.$refs.audioSource

    var music = _this.albums[0];
    audioObj.ontimeupdate = this.getCurrentTime
    audioObj.onended = this.songendHandle
    for (var i = 0; i < 100; i++){
      console.log( Math.floor(( Math.random() * (this.albums.length) )))
    }
    this.loadMusic(music);
    // var ctx = new AudioContext();
    // this.analyser = ctx.createAnalyser();
