    // this.canvasProp.cheight = this.canvas.height
    // this.canvasProp.meterWidth = 1,
    // this.canvasProp.gap = 1,
    // this.canvasProp.lineColor = '#fff',
    // this.canvasProp.meterNum = 100, //count of the meters
    // this.canvasProp.capYPositionArray = []; ////store the vertical position of hte caps for the preivous frame
    // //bg color
    //   this.ctx = this.canvas.getContext('2d');
    //   var bg_gradient = this.ctx.createLinearGradient(0, 0, 0, 300);
    //   bg_gradient.addColorStop(0, "#AFC5FF");
    //   bg_gradient.addColorStop(1, "#FACEFF");
    //   this.canvasProp.bg_gradient = bg_gradient
    //   this.renderFrame();
  }
}


</script>

